import './App.css';

import React, { useEffect } from 'react';

import Navbar from './components/Navbar';

import useMobile from './hooks/useMobile';

import SanMarino1 from './img/sanmarino/san_marino_1.jpg';
import SanMarino2 from './img/sanmarino/san_marino_2.jpg';
import SanMarino3 from './img/sanmarino/san_marino_3.jpg';
import SanMarino4 from './img/sanmarino/san_marino_4.jpg';

function Gallery(props: { images: { src: string, alt: string }[] }) {
    return (
        <div className='Gallery'>
            {props.images.map((image) => {
                return (
                    <div className='Gallery__image'>
                        <a href={image.src} target='_blank'><img src={image.src} alt={image.alt} width={'100%'} /></a>
                    </div>
                )
            })}
        </div>
    )
}

function Wycieczka(props: { title: string, text: string, images: { src: string, alt: string }[] }) {
    return (
        <div className='Wycieczka'>
            <div className='Wycieczka__title'>
                {props.title}
            </div>
            <Gallery images={props.images} />
            <div className='Wycieczka__text'>
                {props.text}
            </div>
            <hr></hr>
        </div>
    )
}

export default function Wycieczki() {
    useEffect(() => {
        window.scrollTo({top: 0});
    }, [])
    const sanmarino_gallery = [
        {
            src: SanMarino4,
            alt: 'San Marino 4'
        },
        {
            src: SanMarino3,
            alt: 'San Marino 3'
        },
        {
            src: SanMarino1,
            alt: 'San Marino 1'
        },
        {
            src: SanMarino2,
            alt: 'San Marino 2'
        },
    ]

    return (
        <>
        <Navbar />
        <div className='App__container'>
            <div className='App'>
                <div className='App__content'>
                    <div className='App__content__title'>
                        Wycieczki i czas wolny
                    </div>
                    <div className='App__content__text'>
                        Uczniowie mieli moliwo spdzenia czasu wolny na wiele ciekawych sposobw.<br></br>Jednym z nich byy wycieczki do przernych miejsc.
                    </div>
                    <Wycieczka title='Wycieczka do San Marino' text='W ramach poznawania kultury wybralimy si do San Marino. Wczeniej kilkoro z nas przygotowao plan wycieczki i przewodnik po tym pastwie. Dzie wczeniej Hubert i Piotr przedstawili nam podstawowe informacje na temat San Marino, jego historii i czasw wspczesnych. Jeszcze przed wyjazdem zwrcilimy uwag na umiowanie wolnoci mieszkacw tego pastwa. W trakcie zwiedzajc starego miasta, szukalimy miejsc, o ktrych mwili koledzy i odkrywalimy nowe. Zachwycia nas topografia tego miasta zbudowanego na skale, podziwialimy  budowle, pomniki, a niektrzy dotarli nawet do twierdzy La Rocca o Guaita i zwiedzili ten obiekt powstay w XI wieku. Kupujc bilet do twierdzy,  nabylimy te moliwo wejcia do siedziby wadz San Marino, Palazzo Pubblico.  Zachwyca szczeglnie  sala parlamentu, w ktrej ciany zdobi pikne malowida cienne. Sala jest uytkowana, na stolikach stoj laptopy, jest kilka telewizorw, zamontowano mikrofony. Odbywaj si tak obrady wadz, a kiedy wadze nie obraduj, turyci mog sal zwiedza.  San Marino umiejtnie czy pikn histori ze wspczesnoci. W miecie wiele jest wspczesnych rzeb i pomnikw upamitniajcych wane i tragiczne wydarzenia, jak te z czasw II wojny wiatowej czy tragedi dzieci z Biesana. Jedn ze wspczesnych atrakcji w dniu naszej wycieczki bya impreza Steam party 2023 oraz wystawa Lego.' images={sanmarino_gallery} />
                </div>
            </div>
            <div className='App__background'></div>
        </div>
        </>
    )
}