import './App.css';

import React, { useEffect } from 'react';

import Navbar from './components/Navbar';
import Banner from './img/erasmus-banner-projekt.png';
import BannerMobile from './img/erasmus-banner-projekt-mobile.png';

import SanMarino1 from './img/sanmarino/san_marino_5.jpg';
import Tyberiusz1 from './img/tyberiusz/339731727_621824943296754_3976686423984603631_n.jpg';
import Tyberiusz2 from './img/tyberiusz/340220609_953004922799556_8873225374493544035_n.jpg';
import useMobile from './hooks/useMobile';
import { useNavigate } from 'react-router-dom';

function Gallery(props: { images: { src: string, alt: string }[] }) {
    return (
        <div className='Gallery'>
            {props.images.map((image) => {
                return (
                    <div className='Gallery__image'>
                        <a href={image.src} target='_blank'><img src={image.src} alt={image.alt} width={'100%'} /></a>
                    </div>
                )
            })}
        </div>
    )
}

const tyberiusz_gallery = [
    {
        src: Tyberiusz1,
        alt: 'Tyberiusz 1'
    },
    {
        src: Tyberiusz2,
        alt: 'Tyberiusz 2'
    },
]

export default function App() {
    useEffect(() => {
        window.scrollTo({top: 0});
    }, [])
    const isMobile = useMobile();
    const navigate = useNavigate();
    return (
        <>
        <Navbar />
        <div className='App__container'>
            <div className='App'>
                <div className='App__banner'>
                    {!isMobile ? <img src={Banner} alt='Banner' width={'100%'}/> : <img src={BannerMobile} alt='Banner' width={'100%'}/>}
                </div>
                <div className='App__content'>
                    <div className='App__content__title'>
                        Cele projektu
                    </div>
                    <div className='App__content__text'>
                        <p>Realizacja stau zawodowego w firmie europejskiej, co pozwoli uczestnikom na:</p>
                        <ul>
                            <li>wzrost praktycznych umiejtnoci zawodowych oraz weryfikacj nabytej wiedzy w rzeczywistych warunkach pracy,</li>
                            <li>doskonalenie umiejtnoci posugiwania si jzykiem angielskim, take zawodowym, w bezporednich kontaktach z mieszkacami Europy,</li>
                            <li>zdobycie praktycznego dowiadczenia zawodowego za granic,</li>
                            <li>poszerzenie wiedzy na temat innych kultur i krajw,</li>
                            <li>ksztatowanie postaw proeuropejskich i spoecznych,</li>
                            <li>nawizywanie kontaktw midzynarodowych, aktywne uczestnictwo w yciu spo. i rozwj poczucia europejskiego obywatelstwa i tosamoci,</li>
                            <li>zapewnienie lepszego uznawania kompetencji zdobytych podczas nauki za granic, dziki certyfikatowi Europass, Indywidualnemu Wykazowi Osigni (ECVET),</li>
                            <li>wspprac dyrekcji i nauczycieli przedmiotw zawodowych z przedstawicielami firm woskich w zakresie ich oczekiwa, moliwoci, programu praktyk i realizacji stay,</li>
                            <li>pogbianie wsppracy szkoy z lokalnym rynkiem pracy,</li>
                            <li>promocj szkoy i programu Erasmus Plus w rodowisku zagranicznych i lokalnym.</li>
                        </ul>
                        <p>Uczestnicy projektu to 20 uczniw klas II, III i IV technikum (technik informatyk, technik rachunkowoci i technik fotografii i multimediw) ksztaccy si w ZSP w Kronie Odrzaskim. Po zajciach przygotowawczych (jzykowych, kulturoznawczych i przygotowujcych mentalnie do wyjazdu) uczestnicy wyjad na 4 tygodniowy sta. We Woszech poznaj obyczaje, kultur, rozwin praktyczn znajomo jzyka angielskiego, a moe nawet naucz si woskiego.</p> 
                    </div>
                    <div className='App__content__title'>
                        Aktualnoci
                    </div>
                    <div className='App__content__text'>
                    <p>Od listopada 2022 roku do marca 2023 przygotowywalimy si do wyjazdu do Woch.</p>
                    <p>Intensywnie pracowalimy nad naszym angielskim, poznawalimy zasady funkcjonowania woskich firm, uczylimy si rozwiazywania konfliktw, komunikacji itp.
                    Poznawalimy te kultur Woch, pastwa o przebogatej historii  i osigniciach artystycznych.</p>

                    <p>Od 16 kwietnia 2023 roku jestemy w Rimini.
                    Nasze pierwsze dni tutaj upyny pod znakiem aklimatyzacji.
                    Wszystko wydawao si nie takie, hotel nie spenia oczekiwa, posiki odmienne ni w Polsce. Niektrzy stresowali si przed pierwszym spotkaniem z woskim pracodawc.</p>
                    <p>Zastanawiali si, jak poradz sobie z porozumieniem, jakie dostan zadania, jednak ju po kilku dniach okazao si, e sprytniejsi koledzy objechali cae miasto autobusami i poznali rne ciekawe miejsca, najczciej nieznane turystom.
                    Fotografowie odbyli pierwsze sesje wieczorne, a wysportowani chopcy wczesnym rankiem pokonywali kilometry wzdu morza.</p>

                    <p>Pierwszego dnia pobytu, w poniedziaek nasza opiekunka, pani Karolina, zorganizowaa nam krtk przechadzk po starwce Rimini, zobaczylimy Tampio Malatestiano, czyli katedr, nazwan tak od imienia wadcy tego miasta XV wieku, Sygismondo Pandolfo Malatesty, uk Augusta, Domus del Chirurgo - zabytki Rimini z pocztkw naszej ery i czasw renesansu.
                    W czwartek wsplnie przespacerowalimy si mostem Tyberiusza, poznalimy San Guliano - zabytkow dzielnic ryback, ktrej pocztki sigaj XI wieku. Dzi stanowi ona jedn z wikszych atrakcji turystycznych miasta. Urzeky nas murale i klimatyczne kawiarenki.</p>
                    
                    <p>Idc wzdu portu jachtowego dotarlimy do molo, gdzie  odpoczywalimy i podziwialimy pikno Adriatyku.</p>
                    <Gallery images={tyberiusz_gallery} />
                    <p>Kilkoro z nas wybrao si ladami Federico Felliniego na cmentarz w Rimini, gdzie znajduje si grb tego, urodzonego w Rimini, synnego reysera i jego nie mniej znanej ony, aktorki Gulietty Masiny.  Okazao si, e miejsce to bardzo rni si od naszych nekropoli, wielkoci grobowcw, ktre wskazuj na ogromne znaczenie wizi rodzinnych dla  Wochw, potrzeb zachowania w pamici bliskich zmarych. Jeszcze bardziej zadziwiy nas bloki z urnami bd trumnami.</p>
                    <p>W niedziel 23 kwietnia 2023 roku wybralimy si do San Marino.</p>
                    <img className='App__content__image' src={SanMarino1} alt='San Marino 1' width={'100%'} style={{borderRadius: '8px'}} onClick={() => navigate('/wycieczki')} />
                    </div>
                </div>
            </div>
        </div>
        </>
    )
}